import React from 'react';
import { ScrollView, StyleSheet, RefreshControl } from 'react-native';
import { Sensors } from './Sensors';

export default class SensorsScreen extends React.Component {
  static navigationOptions = {
    title: 'Sensors'
  };

  constructor(props) {
    super(props);
    this.state = {
      items: [],
      refreshing: false,
    }
  }

  reloadData = () => {
    this.setState({refreshing: true});
    fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensorValues?from=6`)
    .then(response => response.json())
    .then(json => this.setState({items: json.series, refreshing: false}, function(){}))
    .catch((error) =>{
      console.error(error);
      this.setState({refreshing: false});
    });
  }

  componentDidMount() {
    this.reloadData();
  }

  render() {
    return (
      <ScrollView style={styles.container}
                  refreshControl={
          <RefreshControl
            refreshing={this.state.refreshing}
            onRefresh={this.reloadData}
          />
        }>
        <Sensors items={this.state.items}/>
      </ScrollView>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    paddingTop: 15,
    backgroundColor: '#fff',
  },
});



