import React from 'react';
import { StyleSheet, Text, View } from 'react-native';
import moment from 'moment';

export class Sensors extends React.Component {
  constructor(props) {
    super(props);

    setInterval(() => {this.forceUpdate();}, 60000);
  }

  getUnitSign(type) {
    switch (type) {
      case "temp": return "C";
      case "humidity": return "%";
      case "lum": return "L";
      case "uv": return "UV";
      default: return "N/A";
    }
  }

  epochToDateTime(epoch) {
    const d = new Date(0);
    d.setUTCSeconds(epoch / 1000);
    return d.toLocaleDateString('sv-SE') + ' ' + d.toLocaleTimeString();
  }

  render() {
    return (
      <View>
        {this.props.items.map((item, i) => {
          return (
            <View
              key={item.sensorid + ":" + item.type}
              style={styles.option}>
              <View style={{ flexDirection: 'column' }}>
                <View style={{ flexDirection: 'row', justifyContent:'space-between'}}>
                  <Text style={styles.optionText}>
                    {item.name.substring(0,35) + (item.name.length > 35 ? "..." : "")}
                  </Text>
                  <Text style={{alignSelf: 'flex-end'}}>
                    {item.data.slice().reverse()[0][1]} {this.getUnitSign(item.type)}
                  </Text>
                </View>
                <Text style={styles.optionText}>
                  {moment(item.data.slice().reverse()[0][0]).fromNow()}
                </Text>
              </View>
            </View>
          )
        })
        }
      </View>
    )
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    paddingTop: 15,
  },
  optionsTitleText: {
    fontSize: 16,
    marginLeft: 15,
    marginTop: 9,
    marginBottom: 12,
  },
  optionIconContainer: {
    marginRight: 9,
  },
  option: {
    backgroundColor: '#fdfdfd',
    paddingHorizontal: 15,
    paddingVertical: 15,
    borderBottomWidth: StyleSheet.hairlineWidth,
    borderBottomColor: '#EDEDED',
  },
  optionText: {
    fontSize: 15,
    marginTop: 1,
  },
});



